import React, { Component } from 'react';
import Nav from 'react-bootstrap/Nav';
import NavBar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import 'bootstrap/dist/css/bootstrap.min.css';

import './NavBarArea.css';

import { Link } from "react-router-dom";

import Icon from '../../components/Icon/Icon.js';
import Label from '../../components/Labels/Label/Label.js';


import logoImage from '../../img/logo.png';
import languageIcon from '../../img/language.png';


export default class NavBarArea extends Component {
    constructor(props) {
        super(props);
        var checkUrl = new URL(window.location.href);
        var lang = checkUrl.searchParams.get("lang");
        if (lang == "en") {
            this.props.languageChange("en");
        }
        else if (lang == "ko") {
            this.props.languageChange("ko");
        }
    }

    T(en, ko) {
        if (this.props.language == "en") {
          return en;
        }
        else {
          return ko;
        }
    }

    render() {
        return (
            <div className="NavBar">
                <div className="nav-bar">
                    <NavBar fixed="top" bg="light" expand="lg">
                        <Link className="navbar-brand" to="/">
                            <img className="nav-icon" src={logoImage} alt="TwitchLink" />
                            TwitchLink
                        </Link>
                        <NavBar.Toggle/>
                        <NavBar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto">
                                <Nav.Item>
                                    <Link className="nav-link" to="/">TwitchLink</Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Link className="nav-link" to="/download">{this.T("Download", "다운로드")}</Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link href="https://github.com/devhotteok/TwitchLink" target="_blank">{this.T("Support & Issues", "지원 & 버그 제보")}</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Link className="nav-link" to="/donate">{this.T("Donate", "후원")}</Link>
                                </Nav.Item>
                            </Nav>
                            <Nav>
                                <NavDropdown title={
                                    <div style={{display: "flex"}}>
                                        <Icon size="small" src={languageIcon} alt="Language" style={{marginRight: "10px"}}/>
                                        <Label>Language / 언어</Label>
                                    </div>
                                }>
                                    <NavDropdown.Item onClick={() => this.props.languageChange("en")}>ENGLISH</NavDropdown.Item>
                                    <NavDropdown.Item onClick={() => this.props.languageChange("ko")}>한국어</NavDropdown.Item>
                                </NavDropdown>
                            </Nav>
                        </NavBar.Collapse>
                    </NavBar>
                    <div className="nav-bar-area"></div>
                </div>
            </div>
        );
    }
}